<template>
  <div class="trendPage secondary-page-mode">
    <div class="secondary-toolbar">
        <span>时间类型 : </span>
         <div class="layoutBox" style="width:120px">
            <el-select v-model="value" placeholder="请选择" @change="selectChange">
                  <el-option
                    v-for="item in optionData"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
            </el-select>
          </div>&emsp;&emsp;
          <span>监测因子 : </span>
         <div class="layoutBox" style="width:120px">
            <el-select v-model="factorModel" placeholder="请选择" @change="selectChange">
                  <el-option
                    v-for="item in factorData"
                    :key="item.projectCode"
                    :label="item.projectName"
                    :value="item.projectCode">
                  </el-option>
            </el-select>
          </div>&emsp;&emsp;
          <!-- <span>数据类型 : </span>
         <div class="layoutBox" style="width:120px">
            <el-select v-model="value1" placeholder="请选择">
                  <el-option
                    v-for="item in optionData1"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
            </el-select>
          </div>&emsp;&emsp; -->
         <span>本期({{timeType}}) : </span>
         <div class="layoutBox" style="width:242px" v-show="dateType == 'daterange'"><el-date-picker v-model="dateRangeDayOne" type="daterange" v-show="dateType == 'daterange'" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" @change="dateRangeChange"></el-date-picker> </div>
         <div class="layoutBox" style="width:140px" v-show="dateType == 'week'" ><el-date-picker v-model="dateRangeWeekOne" type="week" format="yyyy 第 WW 周" placeholder="请选择" value-format="yyyy-MM-dd"></el-date-picker> </div>
         <div class="layoutBox" style="width:140px" v-show="dateType == 'month'"><el-date-picker v-model="dateRangeMonthOne" type="month"  placeholder="请选择" value-format="yyyy-MM"></el-date-picker> </div>
         <yf-quarter v-show="dateType == 'quarter'" @getQuar='setQuerterOne'></yf-quarter>
         <div class="layoutBox" style="width:140px" v-show="dateType == 'year'"><el-date-picker v-model="dateRangeYearOne" type="year"  placeholder="请选择" value-format="yyyy"></el-date-picker> </div>&emsp;&emsp;
         <span>对比({{timeType}}) : </span>
         <div class="layoutBox" style="width:242px" v-show="dateType == 'daterange'"><el-date-picker v-model="dateRangeDayTwo" type="daterange"  start-placeholder="开始日期" end-placeholder="结束日期" value-format="yyyy-MM-dd" disabled></el-date-picker> </div>
         <div class="layoutBox" style="width:140px" v-show="dateType == 'week'" ><el-date-picker v-model="dateRangeWeekTwo" type="week" format="yyyy 第 WW 周" placeholder="请选择" value-format="yyyy-MM-dd"></el-date-picker> </div>
         <div class="layoutBox" style="width:140px" v-show="dateType == 'month'"><el-date-picker v-model="dateRangeMonthTwo" type="month"  placeholder="请选择" value-format="yyyy-MM"></el-date-picker> </div>
         <yf-quarter v-show="dateType == 'quarter'" @getQuar='setQuerterTwo'></yf-quarter>
         <div class="layoutBox" style="width:140px" v-show="dateType == 'year'"><el-date-picker v-model="dateRangeYearTwo" type="year"  placeholder="请选择" value-format="yyyy"></el-date-picker> </div>&emsp;&emsp;
        <el-button @click="search"><i class="iconfont icon-sousuo"></i>查询</el-button>
    </div>
    <div style="position:relative" class="currency-mode-wrap" v-loading="loading"  element-loading-text="加载中"
               element-loading-spinner="loading-type-a"
               element-loading-background="rgba(255, 255, 255, 1)">
           <div class="echartbox" id="echartbox">

           </div>
             <yf-Datastatus :dataStatus="dataStatus" v-if="dataStatus == 'no'"></yf-Datastatus>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      value:1,
      dateRangeDayOne:['2019-08-01','2019-08-10'],
      dateRangeDayTwo:['2019-08-11','2019-08-20'],
      dateRangeWeekOne:'',
      dateRangeWeekTwo:'',
      dateRangeMonthOne:'',
      dateRangeMonthTwo:'',
      dateRangeYearOne:'',
      dateRangeYearTwo:'',
      querterOne:{},
      querterTwo:{},
      optionData: [
          { label: "日", value: 1 },
          { label: "周", value: 2 },
          { label: "月", value: 3 },
          { label: "季", value: 4 },
          { label: "年", value: 5 }
      ],
      // optionData1: [
      //     { label: "数据值", value: 1 },
      //     { label: "平均值", value: 2 },
      //     { label: "中位值", value: 3 }
      // ],
      timeType: "日",
      dateType: "daterange",
      factorData:[],
      factorModel:'',
      legendData: [],
      xAxisData: [],
      seriesData: [],
      stationId:'',
      dataStatus: "",
      loading:true
    };
  },
  created() {
     this.getFactors();
     this.getEchartData()
  },
  mounted(){
    // this.$nextTick(()=>{
    //     this.initEchart()
    // }) 
  },
  methods: {
    selectChange(arr) {
      for (let i in this.optionData) {
        if (arr == this.optionData[i].value) {
          this.timeType = this.optionData[i].label;
        }
      }
      if (this.timeType == "日") {
        this.dateType = "daterange";
      } else if (this.timeType == "周") {
        this.dateType = "week";
      } else if (this.timeType == "月") {
        this.dateType = "month";
      } else if (this.timeType == "季") {
        this.dateType = "quarter";0
      } else if (this.timeType == "年") {
        this.dateType = "year";
      }
    },
    //获取监测因子
    getFactors(){
        this.$http.post("/SiteManagement/index/query").then(res => {
        if (res.data.code == 0) {
          this.factorData = res.data.content.dataList;
          this.factorModel=this.factorData[0].projectCode
        }
      });
    },
    //时间类型为日时，时间改变，默认填充第二个时间框时间
    dateRangeChange(){
       // console.log(this.dateRangeDayOne);

    },
    initEchart(xAxisData,seriesData,legendData) { 
      var option = {
        title: {
          text: legendData[0]+"变化分析图",
          left: "center"
        },
        tooltip: {
          trigger: "axis"
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          data:xAxisData,
          splitLine: {
            show: false
          },
          boundaryGap: false
        },
        yAxis: {
          splitNumber: 3,
          name: "监测值",
          splitLine: {
            show: false
          }
        },
        series: seriesData
        ///[
        //   {
        //     name: "时段1",
        //     type: "line",
        //     data: [100, 80, 50, 85, 90, 40, 50, 60, 270],
        //     itemStyle: {
        //       normal: {
        //         color: "#c23531"
        //       }
        //     },
        //   },
        //   {
        //     name: "时段2",
        //     type: "line",
        //     data: [200, 180, 150, 185, 190, 140, 150, 160, 60],
        //     itemStyle: {
        //       normal: {
        //         color: "#c23531"
        //       }
        //     },
        //   },
        // ]
      };
      var myChart = this.$echarts.init(document.getElementById("echartbox"));
      //使用制定的配置项和数据显示图表
      myChart.setOption(option);
    },
    search(){
     
        
       
        this.getEchartData()
    },
    //获取季度组件传出的数据
    setQuerterOne(obj){
      this.querterOne=obj
    },
    setQuerterTwo(obj){
       this.querterTwo=obj
    },
    //获取绘图数据
    getEchartData(){
      this.loading=true;
       //根据时间选择类型组装搜索参数
        let params={};
        if (this.timeType == "日") {
           params={
              stationCode:this.stationId,
              factorCode:this.factorModel,
              startTime:this.dateRangeDayOne[0],
              endTime:this.dateRangeDayOne[1],
              startQuarter:'',
              endQuarter:'',
              type:'day',
              compareStart:this.dateRangeDayTwo[0],
              compareEnd:this.dateRangeDayTwo[1],
           }
        } else if (this.timeType == "周") {
            var now = new Date(this.dateRangeWeekOne);
            var nowTime = now.getTime() ;
            var day = now.getDay();
            var oneDayTime = 24*60*60*1000 ;
            var MondayTime = nowTime - (day)*oneDayTime ;//显示周一
            var SundayTime =  nowTime + (6-day)*oneDayTime ;//显示周日

            var af = new Date(this.dateRangeWeekTwo);
            var afTime = af.getTime() ;
            var day = af.getDay();
            var MondayTime1 = afTime - (day)*oneDayTime ;//显示周一
            var SundayTime1 =  afTime + (6-day)*oneDayTime ;//显示周日

            params={
              stationCode:this.stationId,
              factorCode:this.factorModel,
              startTime:new Date(MondayTime).format('yyyy-MM-dd'),
              endTime:new Date(SundayTime).format('yyyy-MM-dd'),
              startQuarter:'',
              endQuarter:'',
              type:'day',
              compareStart:new Date(MondayTime1).format('yyyy-MM-dd'),
              compareEnd:new Date(SundayTime1).format('yyyy-MM-dd'),
           }
        } else if (this.timeType == "月") {
            params={
              stationCode:this.stationId,
              factorCode:this.factorModel,
              startTime:this.dateRangeMonthOne,
              endTime:this.dateRangeMonthOne,
              startQuarter:'',
              endQuarter:'',
              type:'month',
              compareStart:this.dateRangeMonthTwo,
              compareEnd:this.dateRangeMonthTwo,
           }
        } else if (this.timeType == "季") {
            console.log(this.querterOne)
             console.log(this.querterTwo)
             params={
              stationCode:this.stationId,
              factorCode:this.factorModel,
              startTime:this.querterOne.year,
              endTime:this.querterOne.year,
              startQuarter:this.querterOne.quarter,
              endQuarter:this.querterTwo.quarter,
              type:'quarter',
              compareStart:this.querterTwo.year,
              compareEnd:this.querterTwo.year,
           }
        } else if (this.timeType == "年") {
          params={
              stationCode:this.stationId,
              factorCode:this.factorModel,
              startTime:this.dateRangeYearOne,
              endTime:this.dateRangeYearOne,
              startQuarter:'',
              endQuarter:'',
              type:'year',
              compareStart:this.dateRangeYearTwo,
              compareEnd:this.dateRangeYearTwo,
           }
        }
       this.$http.get("/StatisticsAndAlarm/dataCharts/annualRatioCharts",{
         params:params
       }).then(res=>{
            this.loading = false;
            if(!res.data.content){
                var myChart = this.$echarts.init(document.getElementById("echartbox"));

                //使用制定的配置项和数据显示图表
                myChart.clear();
                this.dataStatus='no';
                return false;
            }else{
              this.dataStatus='';
            }
           if(res.data.code == 0){
             this.xAxisData=res.data.content.info.xAxis;
             this.seriesData=res.data.content.info.series;
             this.legendData=res.data.content.info.legend;
             var myChart = this.$echarts.init(document.getElementById("echartbox"));
      
             myChart.clear();
             this.initEchart(this.xAxisData,this.seriesData,this.legendData)
           }
       })
    }
  },
  computed: {
    stationInfo: {
      get: function() {
        return this.$store.state.stationInfo;
      }
    },
    
  },
  watch: {
    stationInfo(value) {
      if (value.length > 0) {
        this.stationId = value[0].nodeId;
        this.getEchartData();
      }
    },
   
  }
};
</script>

<style scope lang="less">
 .echartbox {
    margin-top: 5px;
    height: 450px;
  }
</style>



